@keyframes animate {
  0% {
    transform: rotate(0deg) translateY(0px); }
  100% {
    transform: rotate(45deg) translateY(-200px); } }

.header__text-smoke ul li:nth-child(1) {
  animation-delay: 0s; }

.header__text-smoke ul li:nth-child(2) {
  animation-delay: .4s; }

.header__text-smoke ul li:nth-child(3) {
  animation-delay: .8s; }

.header__text-smoke ul li:nth-child(4) {
  animation-delay: 1.2s; }

