/*для js */
.error {
  border: 4px solid #e74246; }

.error.phone::placeholder,
.error.name::placeholder,
.error.message::placeholder {
  color: #e74246;
  animation: shake .8s; }

.contact-error {
  animation: shake .6s; }

.contact-show {
  display: block;
  animation: bounce .6s; }

/* Анимация при ошибке*/
@keyframes bounce {
  0% {
    transform: translateY(-2000px); }
  70% {
    transform: translateY(30px); }
  90% {
    transform: translateY(-10px); }
  100% {
    transform: translateY(0); } }

@keyframes shake {
  0%,
  100% {
    transform: translateX(0); }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-10px); }
  20%,
  40%,
  60%,
  80% {
    transform: translateX(10px); } }

